.container{
    width: 500px;
    padding: 11px;
    background-color: white;
    margin: auto;
    margin-top: 53px;
    border-radius: 7px;
}
h1{
    text-align: center;
}
body{
    background-color: rgb(3, 3, 53);
}
.options{
    margin-left: 32px;
}
.qns {
    display: flex;
    justify-content: left;
    
}
.opts{
    display: flex;
    align-items: center;
     margin-top: -22px;
}
.options input{
    width: 25px;
    height: 25px;
    background-color: green;
    cursor: pointer;
}
.options p{
    font-size: 20px;
    margin-left: 11px;
    border-radius: 50%;
}
.btn{
    display: flex;
    justify-content: center;
}
.btn button{
    width: 140px;
    height: 40px;
    background-color: rgb(1, 1, 49);
    color: white;
    font-size: 22px;
    border: none;
    cursor: pointer;
    border-radius: 2px;
}
.score{
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    
}
.score p{
    font-size: 44px;
}